 } */

     body {
         background-color: #fff;
         height: 100vh;
         width: 100vw;
         margin: 0;
         padding: 0;
         outline: none;
         display: flex;
         flex-direction: column;
         justify-content: center !important;
         align-items: center !important;
     }

     input:focus,
     select:focus,
     textarea:focus,
     button:focus,
     div:focus {
         outline: 0;
     }

     div:focus .quickReply {
         border: 1px dotted #333;
         background-color: #58d358;
         -webkit-tap-highlight-color: transparent;
         /* Removes the blue highlight when button tapped on mobile */
     }

     /* This 'containerEunit' class literally does nothing; but leave it there! ðŸ™‚ */
     .containerEunit {
         background-color: inherit;
     }

     .App {}

     .botIcon {
         position: fixed;
         left: -200px;
         top: 180px;
         width: 400px;
     }

     .chatbotOutput {
         background-color: rgba(255, 255, 255, 0.7);
         box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
         text-align: left;
         font-family: Ubuntu, sans-serif;
         border: 0.1pt solid lightgray;
         position: relative;
         justify-content: left;
         font-size: 20px;
     }

     /* Unrecognized devices START */

     .botOutputsContainer>div:last-child {
         margin: 0 0 300px 0;
         /* Gives the last .chatbotOutput (chat bubble) some space below; I guess for scrolling? ðŸ™‚ */
     }

     .botOutputsContainer {
         height: 100vh;
         min-height: 100vh;
         max-height: 100vh;
         overflow-y: scroll;
         scroll-behavior: smooth;
         position: fixed;
         left: 25%;
         top: 0;
         right: 5%;
         padding: 0;
         margin: 0;
         width: 100%;
     }

     .chatbotOutput {
         width: 60vw;
         max-width: 800px;
         line-height: 36px;
         border-radius: 10px;
         padding: 45px;
         display: inline-block;
         margin: 0 0 14px 0;
     }


     .quickReplyContainer {
         display: flex;
         flex-direction: row;
         flex-wrap: wrap;
         position: absolute;
         left: 25%;
         bottom: 15%;
         width: 40%;
         word-wrap: normal;
         white-space: nowrap;
         padding: 10px;
         margin: 12px auto;
     }

     .quickReply {
         font-size: 17px;
         height: 26px;
         border-radius: 12px;
         margin: 20px 8px 8px;
         padding: 8px 12px;
         cursor: pointer;
         line-height: 50px;
     }

     /* Unrecognized devices END */


     .quickReply {
         text-align: center;
         font-family: Ubuntu, sans-serif;
         text-decoration: none;
         text-transform: capitalize;
         outline: none;
         cursor: pointer;
         font-style: normal;
         font-weight: 400;
         color: rgb(0, 0, 0);
         background-color: #72b352;
         border: 1px solid #f5f5f5;
         border-image: initial;
         -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
         filter: alpha(opacity=80);
         opacity: 0.8;
         -moz-opacity: 0.8;

     }

     .quickReply:hover {
         background-color: #67AB67;
     }

     .inputFieldContainer {
         position: fixed;
         margin-left: 5%;
         margin-right: 5%;
         max-width: 800px;
         width: 70%;
         left: 20%;
         bottom:10%;
     }

     .inputField {
         z-index: 9999;
         font-family: Ubuntu, sans-serif;
         font-size: large;
         align-items: center;
         background-color: rgba(255, 255, 255, 0.9);
         box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25);
         border-radius: 10px;
         display: flex;
         justify-content: center;
         margin-right: 4rem;
         padding-left: 15px;
         width: 100%;
         height: 50px;
     }

     input {
         padding: 5px;
     }

     button {
         z-index: 9999;
         position: absolute;
         right: -10px;
         top: 7px;
         width: 70px;
         height: 45px;
         -moz-appearance: none;
         border: none;
         background: #ededed;
         border-radius: 3px;
         padding: 10px;
         margin-top: 2px;
         text-transform: capitalize;
         transition: all .2s;
     }

     button:hover {
         background-color: #67AB67;
         color: white;
     }

     button:active {
         background-color: #468546;
         color: white;
     }

     /* Smaller Smarthphones */
     @media only screen and (min-width: 320px) and (max-width: 480px) {


         .botOutputsContainer {
             left: 5%;
             top: 0;
         }

         .chatbotOutput {
             min-width: 220px;
             width: 70vw;
             line-height: 35px;
             padding: 14px 28px;
             display: inline-block;
             margin: 0 14px 14px 0;
         }

         .inputFieldContainer {
             position: fixed;
             margin-left: 5%;
             margin-right: 5%;
             max-width: 800px;
             width: 70%;
             left: 7%;
             bottom: 1%;
         }

         .quickReplyContainer {
             position: absolute;
             left: 9%;
             bottom: 12%;
             width: 70%;
         }

         .quickReply {
             line-height: 35px;
             font-size: 14px;
             height: 20px;
             border-radius: 10px;
             margin: 20px 6px 6px;
             padding: 6px 10px;
         }
     }

     /* Tablets */

     @media only screen and (min-width: 481px) and (max-width: 768px) {

         .botOutputsContainer {
             top: 0;
             left: 5%;
         }

         .chatbotOutput {
             width: 70vw;
             max-width: 70vw;
             line-height: 36px;
             border-radius: 10px;
             padding: 45px;
             display: inline-block;
             margin: 0 0 14px 0;
         }

         .quickReplyContainer {
             position: absolute;
             left: 15%;
             bottom: 15%;
             width: 70%;
         }

         .quickReply {
             font-size: 14px;
             height: 20px;
             border-radius: 10px;
             margin: 20px 6px 6px;
             padding: 6px 10px;
             line-height: 35px;
         }

         .inputFieldContainer {
             position: fixed;
             margin-left: 5%;
             margin-right: 5%;
             max-width: 800px;
             width: 70%;
             left: 10%;
             bottom: 8%;
         }
     }


     /* Larger Screens */

     @media only screen and (min-width: 769px) and (max-width: 1279px) {

         .quickReplyContainer {
             position: absolute;
             left: 17%;
             bottom: 15%;
             width: 70%;
             margin-left: 5%;
             margin-right: 5%;
         }

         .quickReply {
             font-size: 14px;
             height: 20px;
             border-radius: 10px;
             margin: 20px 6px 6px;
             padding: 6px 10px;
         }

         .inputFieldContainer {
             position: fixed;
             margin-left: 5%;
             margin-right: 5%;
             width: 70%;
             left: 17%;
             bottom: 8%;
         }
     }

     @media only screen and (max-height: 600px) {
         .quickReplyContainer {
             position: absolute;
             bottom: 20%;
             width: 70%;
         }
     }
